<template>
  <div class="my-header" :class="{'online':online}">
    <div class="logo-text"><img src="../images/text-logo.png" alt=""></div>
    <div class="home" @click="goHome"><img src="../images/ico-home.png" alt=""></div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'advert',
  computed: {
    ...mapGetters([
      'online'
    ])
  },
  data () {
    return {}
  },
  created () {
  },
  methods: {
    goHome () {
      this.$router.push({path: '/index'})
    }
  }
}

</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "common/styles/variable.scss";
.my-header {
  flex: 0 0 48px;
  width: 100%;
  height: 48px;
  position: relative;
  background: $orange;
  color: $white;
  box-shadow: 0 4px 16px rgba(0,0,0,0.12);
  &.online {
    background: $blue;
  }
  .logo-text {
    position: absolute;
    top: 8px;
    left: 20px;
  }
  .home {
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
</style>



